import { Component, OnInit } from '@angular/core';

import { NavController, LoadingController, ToastController, AlertController } from '@ionic/angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ErrorService } from '../../services/error.service';
import { AuthService } from '../../services/auth.service';
import { NativeStorage } from '@ionic-native/native-storage/ngx';

@Component({
    selector: 'login',
    templateUrl: './login.page.html',
    styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {


   
    displayErrorMessage: boolean = false;
    // showPassword: boolean = false;
    //loginFormGroup: FormGroup;

    public loginFormGroup: FormGroup;

    constructor(public navController: NavController,
        public loadingCtrl: LoadingController,
        public errorService: ErrorService,
        private toastCtrl: ToastController,
        private formBuilder: FormBuilder,
        private authService: AuthService,
        public loadingController: LoadingController,
        public alertController: AlertController) {

        // this.loginFormGroup = new FormGroup({
        //     email: new FormControl('', Validators.required),
        //     password: new FormControl('',Validators.required),
        //   });

        this.loginFormGroup = this.formBuilder.group({
            email: ["", Validators.compose([Validators.required])],
            password: ["", Validators.compose([Validators.required])],
            showPassword: [false]
        });
    }


    async presentLoggedInToast(emailAddress: string) {
        let toast = await this.toastCtrl.create({
            message: "You have successfully logged in as " + emailAddress,
            duration: 3000,
            position: "bottom"
        });

        await toast.present();
    }

    async presentAlert(message: string) {
        const alert = await this.alertController.create({
            header: 'Failed to login',
            message: message,
            buttons: ['OK']
        });

        await alert.present();
    }

    async login() {

        const loading = await this.loadingController.create({
            message: 'Please wait...'
        });

        await loading.present();
        var email = this.loginFormGroup.value.email;
        var password = this.loginFormGroup.value.password;

        var result = await this.authService.signIn(email, password);
        await loading.dismiss();
        if(!result){
            this.presentAlert("Incorrect username and/or password");
        }
        else{
            this.navController.navigateRoot("search");
            await this.presentLoggedInToast(email);
        }
          
    
    }

    forgotPassword() {
        this.navController.navigateForward("forgot-password");
    }

    //redirects user to register page
    register() {
        this.navController.navigateForward("register");
    }

    ngOnInit() {

    }

}
